export const Button = ({
  children = "Click me",
  type,
  size = "medium",
  variant = "square",
}) => {
  return (
    <button
      className={[cl.button, cl[size], cl[variant]].join(" ")}
      type={type}>
      {children}
    </button>
  );
};
